export const translateService = {
    getTranslatedMediaType,
    getTranslatedParticipantsType,
    getTranslatedTimeOfDayList
}

function getTranslatedMediaType(mediaType){
    switch (mediaType) {
        case 'VIDEO': return 'וידאו'
        case 'AUDIO': return 'אודיו'
        case 'TEXT': return 'טקסט'
        default: return ''
    }
}

function getTranslatedParticipantsType(participantsType){
    switch (participantsType) {
        case 'KIDS': return 'ילדים'
        case 'ADULTS': return 'מבוגרים'
        case 'ENTIRE_FAMILY': return 'כל המשפחה'
        default: return ''
    }
}

function getTranslatedTimeOfDayList(timeOfDay){
    return timeOfDay.map(time => {
        switch (time) {
            case 'MORNING': return 'בוקר'
            case 'AFTERNOON': return 'אחר הצהריים'
            case 'EVENING': return 'ערב'
            case 'NIGHT': return 'לילה'
            default: return ''
        }
    })
}